<template>
  <v-dialog
        v-model="dialog"
        max-width="300"
      >
      <template v-slot:activator="{ on, attrs }">
        <v-list-item
        link
        :key=Profile
        v-bind="attrs"
        v-on="on"
        >
          <v-list-item-action class="mr-3">
            <v-icon
              color="third"
            >
            mdi-account-circle
          </v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>
              My Profile
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>

      </template>
      <v-card
   class="overflow-hidden"
   color="primary"
   dark
 >
   <v-toolbar
     flat
     color="secondary"
   >
     <v-icon>mdi-account</v-icon>
     <v-toolbar-title class="font-weight-light">
       User Profile
     </v-toolbar-title>
     <v-spacer></v-spacer>
     <v-btn
       color="accent"
       fab
       small
       @click="isEditing = !isEditing"
     >
       <v-icon v-if="isEditing">
         mdi-close
       </v-icon>
       <v-icon v-else>
         mdi-pencil
       </v-icon>
     </v-btn>
   </v-toolbar>
   <v-card-text>
     <v-text-field
       :disabled="!isEditing"
       color="white"
       label="Name"
     ></v-text-field>
     <v-autocomplete
       :disabled="!isEditing"
       :items="options"
       :filter="customFilter"
       color="white"
       item-text="name"
       label="Occupation"
     ></v-autocomplete>
   </v-card-text>
   <v-divider></v-divider>
   <v-card-actions>
     <v-spacer></v-spacer>
     <v-btn
       :disabled="!isEditing"
       color="success"
       @click="save"
     >
       Save
     </v-btn>
   </v-card-actions>
   <v-snackbar
     v-model="hasSaved"
     :timeout="2000"
     absolute
     bottom
     left
   >
     Your profile has been updated
   </v-snackbar>
 </v-card>
      </v-dialog>
</template>

<script>
export default {
  name: 'Profile',
  data () {
      return {
        hasSaved: false,
        isEditing: null,
        model: null,
        options: [
          { name: 'Fisherman', id: 1 },
          { name: 'Fish', id: 2 },
          { name: 'Consumer', id: 3 },
        ],
      }
    },

    methods: {
      customFilter (item, queryText, ) {
        const textOne = item.name.toLowerCase()
        const textTwo = item.abbr.toLowerCase()
        const searchText = queryText.toLowerCase()

        return textOne.indexOf(searchText) > -1 ||
          textTwo.indexOf(searchText) > -1
      },
      save () {
        this.isEditing = !this.isEditing
        this.hasSaved = true
      },
    },
}
</script>
