import Vue from 'vue'
import VueRouter from 'vue-router'
import Car from '../views/Car.vue'
import About from '../views/About.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Car',
    component: Car
  },
  {
    path:'/',
    name: 'About',
    component: About
  }
]


const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
