import Vue from 'vue'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'

Vue.use(Vuetify)

const opts = {
  theme: {
    themes: {
      light:{
        background: 'cae9ff',
        primary: '#269EE3',
        secondary: '#81c6ef',
        accent: '#dd6e42',
        error: '#c44900',
        third: '#b68463',
        info: '#cae9ff',
        success: '#09862c',
        warning: '#FFC107',
      },
      dark: {
        primary: '#1976D2',
        secondary: '#424242',
        accent: '#82B1FF',
        error: '#FF5252',
        info: '#2196F3',
        success: '#4CAF50',
        warning: '#FFC107',
      },
    },
  },
}

export default new Vuetify(opts)
