<template>
  <v-dialog
        v-model="dialog"
        persistent
        max-width="290"
      >
      <template v-slot:activator="{ on, attrs }">
        <v-list-item
        link
        :key=SignOut
        v-bind="attrs"
        v-on="on"
        >
          <v-list-item-action class="mr-3">
            <v-icon
              color="third"
            >
            mdi-logout-variant
          </v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>
              Sign Out
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>

      </template>
        <v-card
          color='secondary'
        >
          <v-card-title
            class="headline"
          >
            Catch ya later!
          </v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="accent"
              @click="dialog = false"
            >
              Sign In
            </v-btn>
            <v-btn
              color="primary"
              @click="dialog2 = true"
            >
              Sign Up
            </v-btn>
          </v-card-actions>
        </v-card>

        <v-dialog
          v-model="dialog2"
          max-width="500px"
          padding="10px"
        >
        <v-card
     class="overflow-hidden"
     color="primary"
     dark
   >
     <v-toolbar
       flat
       color="secondary"
     >
       <v-toolbar-title class="font-weight-light">
         Sign Up
       </v-toolbar-title>
       <v-spacer></v-spacer>
       <v-btn
         color="accent"
         fab
         small
         @click="isEditing = !isEditing"
       >
         <v-icon v-if="isEditing">
           mdi-close
         </v-icon>
         <v-icon v-else>
           mdi-pencil
         </v-icon>
       </v-btn>
     </v-toolbar>
     <v-card-text>
       <v-text-field
         :disabled="!isEditing"
         color="white"
         label="Name"
       ></v-text-field>
       <v-autocomplete
         :disabled="!isEditing"
         :items="options"
         :filter="customFilter"
         color="white"
         item-text="name"
         label="Occupation"
       ></v-autocomplete>
     </v-card-text>
     <v-divider></v-divider>
     <v-card-actions>
       <v-spacer></v-spacer>
       <v-btn
         :disabled="!isEditing"
         color="success"
         @click="save"
       >
         Save
       </v-btn>
     </v-card-actions>
     <v-snackbar
       v-model="hasSaved"
       :timeout="2000"
       absolute
       bottom
       left
     >
       Your profile has been saved
     </v-snackbar>
   </v-card>
        </v-dialog>

      </v-dialog>
</template>

<script>
export default {
  name: 'SignOut',
  data() {
    return {
      dialog: false,
      dialog2: false,
      hasSaved: false,
      isEditing: null,
      model: null,
      options: [
        { name: 'Fisherman', id: 1 },
        { name: 'Fish', id: 2 },
        { name: 'Consumer', id: 3 },
      ],
    }
  },

  methods: {
    customFilter (item, queryText, ) {
      const textOne = item.name.toLowerCase()
      const textTwo = item.abbr.toLowerCase()
      const searchText = queryText.toLowerCase()

      return textOne.indexOf(searchText) > -1 ||
        textTwo.indexOf(searchText) > -1
    },
    save () {
      this.isEditing = !this.isEditing
      this.hasSaved = true
    },
  },
 }
 </script>
