<template>
  <div>
    <v-app-bar
      app
      flat
      short
      clipped-left
      color="secondary"
    >
      <v-app-bar-nav-icon  @click.stop="drawer = !drawer" />
      <v-toolbar-title>Fisherman Synch</v-toolbar-title>
      <v-spacer />
      <v-toolbar
      dense
      flat
      color="secondary"
    >
      <v-text-field
        color="third"
        hide-details
        prepend-icon="mdi-magnify"
        single-line
      >
    </v-text-field>
    </v-toolbar>
    <v-spacer />
      <v-btn icon color='third' text @click="helpDocWindow = true">
        <v-icon
          size="25px"
        >
          {{icons.mdiHelpCircle}}
        </v-icon>
      </v-btn>
      <WalletHolder />
      <Avatar />
    </v-app-bar>
  </div>
</template>

<script>
  import WalletHolder from "./WalletHolder";
  import Avatar from "./Avatar";
  import {mdiHelpCircle} from '@mdi/js';

  export default {
    name: 'AppToolbar',
    data: () => ({
      icons: {
        mdiHelpCircle
      }
    }),
    components: {
      WalletHolder,
      Avatar
    },
    computed: {
      drawer: {
        get() {
          return this.$store.state.layout.drawer
        },
        set(data) {
          this.$store.commit('layout/SET', {type: 'drawer', data: data})
        }
      },
      helpDocWindow: {
        get() {
          return this.$store.state.layout.helpDocWindow
        },
        set(data) {
          this.$store.commit('layout/SET', {type: 'helpDocWindow', data: data})
        }
      }
    }
  }
</script>

