<template>
  <v-dialog
    v-model="helpDocWindow"
    max-width="800"
  >
    <v-card>
      <v-toolbar flat dense>
        <v-toolbar-title>Help</v-toolbar-title>
        <v-spacer />
        <v-btn icon

        text @click="helpDocWindow = false">
          <v-icon>
            mdi-close
          </v-icon>
        </v-btn>
      </v-toolbar>
      <v-tabs vertical>
        <v-tab>
          About
        </v-tab>
        <v-tab>
          How do I use this?
        </v-tab>
        <v-tab>
          Wallet
        </v-tab>

        <v-tab-item>
          <v-card flat>
            <v-card-text >
              <div class="green--text"> What is Fisherman Synch?</div>
              <div class="black--text mb-2">
               Fisherman Synch is an app developed to increase data integrity and transparency. With this functionality, consumers can make more educated choices when choosing where to purchase their fish.
              </div>

            </v-card-text>
          </v-card>
        </v-tab-item>
        <v-tab-item>
          <v-card flat>
            <v-card-text>
              <div class="green--text">How does Fisherman Synch work?</div>
              <div class="black--text mb-2">
                <div>For Fisherman:</div>
                <div class="ml-2">1. A Fisherman catches fish and posts the information about the catch to this app, which stores the information on SIMBAChain's blockchain services.</div>
                <div class="ml-2">2. When the Fisherman wants to sell their catch, they can post the sales information including price. </div>
                <div class="ml-2">3. The consumer can view sales information and puchase the fish. This will mark the transaction closed.</div>
                <div>For fish consumers:</div>
                <div class="ml-2">The consumer can browse fish and view sales information and choose what they would like to purchase.</div>
              </div>

            </v-card-text>
          </v-card>
        </v-tab-item>

        <v-tab-item>
          <v-card flat>
            <v-card-text >
              <div class="green--text">Where is my wallet?</div>
              <div class="black--text mb-2">
                It is at the top corner with an icon <v-icon>mdi-wallet</v-icon>
              </div>
              <div class="green--text">Do I need to create/restore a wallet?</div>
              <div class="black--text mb-2">
                No. It is auto generated once you loaded this app. You are free to save the seed of the wallet.
              </div>
              <div class="green--text">Why does the balance shows N/A?</div>
              <div class="black--text mb-2">
                This app is running on Quorum blockchain which does not require Ether for transactions.
              </div>
            </v-card-text>
          </v-card>
        </v-tab-item>
      </v-tabs>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    computed: {
      helpDocWindow: {
        get() {
          return this.$store.state.layout.helpDocWindow
        },
        set(data) {
          this.$store.commit('layout/SET', {type: 'helpDocWindow', data: data})
        }
      }
    }
  }
</script>
