<template>
  <v-navigation-drawer
    v-model="drawer"
    app
    width="200"
    floating
    overflow
    clipped
  >
    <v-list class="font-weight-regular" style="margin-top: -8px;">
    <About />
    <template v-for="item in items">
        <v-list-item
          link
          :key="item.text"
          @click="item.method"
        >
          <v-list-item-action class="mr-3">
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>
              {{ item.text }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </template>
    </v-list>
  </v-navigation-drawer>

</template>

<script>
  import About from '../../views/About'
  export default {
    name: 'NavDrawer',
    data: () => ({
      items: [
        { text: 'SIMBAChain', icon: 'mdi-web', method: () => window.open('https://simbachain.com/', "_blank")},
        { text: 'IUPUI', icon: 'mdi-school-outline', method: () => window.open('https://iupui.edu', "_blank")},



      ],
    }),
    components:{
      About
    },
    computed: {
      drawer: {
        get() {
          return this.$store.state.layout.drawer
        },
        set(data) {
          this.$store.commit('layout/SET', { type: 'drawer', data: data })
        }
      }
    }
  }
</script>
