<template>
  <v-dialog
        v-model="dialog"
        max-width="290"
      >
      <template v-slot:activator="{ on, attrs }">
        <v-list-item
        link
        :key=Settings
        v-bind="attrs"
        v-on="on"
        >
          <v-list-item-action class="mr-3">
            <v-icon
              color="third"
            >
            mdi-cog
          </v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>
              Settings
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>

      </template>
        <v-card>
          <v-list
              three-line
              subheader
            >
              <v-subheader>General</v-subheader>

              <v-list-item>
                <v-list-item-action>
                  <v-checkbox v-model="mode"></v-checkbox>
                </v-list-item-action>
                <v-list-item-content>
                  <v-list-item-title>Dark Mode</v-list-item-title>
                  <v-list-item-subtitle>Turn on dark mode</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>

              <v-list-item>
                <v-list-item-action>
                  <v-checkbox v-model="notifications"></v-checkbox>
                </v-list-item-action>
                <v-list-item-content>
                  <v-list-item-title>Notifications</v-list-item-title>
                  <v-list-item-subtitle>Notify me about updates to my transactions</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>


              <v-list-item>
                <v-list-item-action>
                  <v-list-item-content>
                  <v-autocomplete
                    :items="options"
                    :filter="customFilter"
                    color="white"
                    item-text="name"
                    label="Language"
                  ></v-autocomplete>
                </v-list-item-content>
              </v-list-item-action>
              </v-list-item>

            </v-list>
        </v-card>
      </v-dialog>
</template>

<script>
export default {
  name: 'Settings',
  data: () => ({
    dialog: false,
    mode: false,
    notifications: false,
    widgets: false,
    options: [
      { name: 'English', id: 1 },
    ],
  }),
  components:{
  },
  computed: {

    drawer: {
      get() {
        return this.$store.state.layout.drawer
      },
      set(data) {
        this.$store.commit('layout/SET', { type: 'drawer', data: data })
      }
    }
  }
}
</script>
